//font base is 16px transform into em use em() function send px and pt as parameter
h1,
.big-title {
    color: black;
    font-size: em(36);
    @extend %text-light;

    .leading {
        font-size: em(64);
    }

    .tracking {
        font-size: em(63);
    }

    @include respond-to('md') {
        font-size: em(60);
    }

    &.title {
        line-height: 1.2;
    }
}

h2,
.header {
    color: black;
    font-size: em(24);
    line-height: 1.35;
    margin: 10px 0;
    @extend %text-light;

    .leading,
    .tracking {
        font-size: em(44);
    }

    @include respond-to('md') {
        font-size: em(36);
    }
}

//h2

h3 {
    color: black;
    font-size: 21px;
    line-height: 1.57;
    @extend %text-regular;

    &.leading,
    &.tracking {
        font-size: em(24);
    }

    &.subtitle {
        line-height: 1.5;
    }

    .c-soon {
        font-size: em(16);
    }
}

h4 {
    color: black;
    font-size: 18px;
    font-weight: bold;

    .leading,
    .tracking {
        font-size: em(23);
    }
}

h6 {
    color: black;
    font-size: 30px;
    line-height: 1.2;
    @extend %text-light;

    .leading,
    .tracking {
        font-size: 36;
    }

    &.header {
        font-size: 30px;
    }
}

//h6

h7 {
    color: black;
    font-size: em(24);

    @extend %text-light;

    .leading,
    .tracking {
        font-size: em(32);
    }
}

.lead,
.subhead {
    @extend %text-light;
    font-size: em(21);
    line-height: 33px;
    max-width: 902px;
    margin: 35px auto;
}

.subhead {
    @extend .lead;
    font-size: 1em;
    line-height: 1.44;

    @include respond-to("md") {
        font-size: 21px;
    }
}

.eye-brow {
    font-size: em(18);
    color: $color-blue;
}

.trade-mark {
    font-size: em(10);
    margin-right: 5px;
}

p {
    &.big-title {
        @extend h1;
    }

    &.check {
        text-indent: -0.75em;
        padding-left: 1.5em;

        i {
            padding-right: 20px;
            color: $color-aquamarine;
        }
    }

    @include respond-to("xxs") {
        font-size: 14px;
    }
}

//p

a {
    color: $color-blue;
    text-decoration: none;

    @include respond-to("xxs") {
        font-size: 14px;
    }

    &.bottom-section-link {
        @extend .lead;
        margin: initial;
        max-width: 100%;
        border-bottom: 1px solid $color-blue;

        &:hover {
            border-bottom: 1px solid darken($color-blue, 20%);
        }
    }

    &:hover {
        outline: none;
        text-decoration: none;
        color: darken($color-blue, 20%);
    }

    &.link-underline {
        text-decoration: underline;
    }

    &.text- {
        &blue {
            color: $color-blue;

            &:focus,
            &:hover {
                color: $color-blue;
            }
        }

        &blue-dark {
            color: $color-text-dark;

            &:focus,
            &:hover {
                color: $color-text-dark;
            }
        }

        &white {
            color: white;

            &:focus,
            &:hover {
                color: white;
            }
        }

        &dark {
            color: $color-alto-dark;

            &:focus,
            &:hover {
                color: $color-alto-dark;
            }
        }

        &green {
            color: $color-green;

            &:focus,
            &:hover {
                color: $color-green;
            }
        }

        &orange {
            color: $color-orange;

            &:focus,
            &:hover {
                color: $color-orange;
            }
        }

        &cyan {
            color: $color-cyan;

            &:focus,
            &:hover {
                color: $color-cyan;
            }
        }

        &aquamarine {
            color: $color-aquamarine;

            &:focus,
            &:hover {
                color: $color-aquamarine;
            }
        }
    }
}

.text- {
    &left {
        text-align: left;
    }

    &right {
        text-align: right;
    }

    &center {
        text-align: center;
    }

    &small {
        font-size: em(12) !important;
        line-height: 1.44 !important;
    }

    &alpha {
        color: rgba(0, 0, 0, 0.5) !important;

        &-white {
            color: rgba(255, 255, 255, 0.5) !important;
        }
    }

    //alpha

    &light {
        @extend %text-light;
    }

    &blue {
        color: $color-blue;
    }

    &blue-dark {
        color: $color-text-dark;
    }

    &white {
        color: white !important;
    }

    &black {
        color: black !important;
    }

    &green {
        color: $color-green;
    }

    &orange {
        color: $color-orange;
    }

    &cyan {
        color: $color-cyan;
    }

    &aquamarine {
        color: $color-aquamarine;
    }
}

//text

.new-design {
    h1 {
        font-weight: 800;
        font-size: 68px;
        letter-spacing: -0.05em;
        line-height: 100%;

        @include respond-to("sm") {
            font-size: 120px;
        }

        @include respond-to("md") {
            font-size: 163px;
        }

        @include respond-to("lg") {
            font-size: 190px;
        }
    }

    h2 {
        font-weight: bold;
        font-size: 40px;
        line-height: 44px;
        letter-spacing: -0.05em;

        @include respond-to("md") {
            font-size: 72px;
            line-height: 92px;
        }
        &.header{
            &.p-t-sm {
                    color: black;
                    font-size: 1.5em;
                    line-height: 1.35;
                    margin: 10px 0;
                    font-weight: 300;
                    font-style: normal;
                    font-stretch: normal;
                    @media (min-width: 992px) {
                        font-size: 2.25em;
                    }
            }
        }
    }

    h3 {
        font-size: 32px;
        line-height: 38px;
        font-weight: 700;
        letter-spacing: -0.05em;

        @include respond-to("sm") {
            font-size: 50px;
            line-height: 58px;
            letter-spacing: 0;
        }
    }

    h4 {
        font-weight: bold;
        font-size: 20px;
        line-height: 26px;

        @include respond-to("sm") {
            font-size: 30px;
            line-height: 40px;
        }

        @include respond-to("md") {
            font-size: 35px;
            line-height: 45px;
        }

        @include respond-to("lg") {
            font-size: 42px;
            line-height: 52px;
        }
    }

    h5 {
        color: $color-alto-dark;
        font-weight: bold;
        font-size: 28px;
        line-height: 36px;
        letter-spacing: -0.035em;

        @include respond-to("lg") {
            font-size: 36px;
            line-height: 42px;
            letter-spacing: 0;
        }
    }

    h6 {
        color: $color-alto-dark;
        font-size: 20px;
        line-height: 26px;
        letter-spacing: -0.035em;
        font-weight: bold;

        @include respond-to("md") {
            font-size: 24px;
            line-height: 32px;
        }
    }

    p {
        color: $color-alto-dark;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;

        @include respond-to('md') {
            line-height: 28px;
        }

        @include respond-to('lg') {
            font-size: 18px;
            line-height: 32px;
        }
    }

    a.arrow-link {
        font-weight: bold;
        font-size: 18px;
        line-height: 24px;
        letter-spacing: -0.035em;
        width: fit-content;

        @include respond-to('md') {
            font-size: 20px;
        }

        &:focus,
        &:hover {
            text-decoration: none;
        }

        &::after {
            content: " ";
            color: $color-text-gray;
            width: 26px;
            height: 26px;
            display: inline-block;
            vertical-align: bottom;
            margin-left: 10px;
        }

        &.white {
            &::after {
                background: url('../images/new-icons/arrow-right-bg-white.svg') no-repeat;
            }

            &:hover {
                &::after {
                    background: url('../images/new-icons/arrow-right-bg-white-hover.svg') no-repeat;
                }
            }
        }

        &.blue {
            &::after {
                background: url('../images/new-icons/arrow-right-bg-blue.svg') no-repeat;
            }

            &:hover {
                color: $color-cta-primary-roll;

                &::after {
                    background: url('../images/new-icons/arrow-right-blue-bg-hover.svg') no-repeat;
                }
            }

            &.full {
                &::after {
                    background: url('../images/new-icons/arrow-right-bg-primary-blue.svg') no-repeat;
                }

                &:hover {
                    color: $color-blue;
                }
            }
        }
    }

    .leadIn {
        font-size: 18px;
        line-height: 24px;
        font-weight: normal;
        color: lighten($color-alto-dark, 20%);

        @include respond-to('md') {
            font-size: 24px;
            line-height: 32px;
            color: lighten($color-alto-dark, 50%);
        }
    }
    
}

.sub-headline {
    font-size: 12px;
    line-height: 18px;
    font-weight: 700;
    display: inline-block;
}