                height: 20px;
                width: 20px;
                vertical-align: middle;
            }

            label {
                font-style: normal;
                font-weight: 700;
                font-size: 20px;
                line-height: 24px;
                color: #005AA3;
                padding-left: 16px;
                vertical-align: -webkit-baseline-middle;
            }

            .mandatory-radios {
                padding: 32px 0;
                display: flex;

                .input-group {
                    display: flex;
                    flex-direction: row;
                    justify-content: center;

                    @include respond-to("sm") {
                        &:first-of-type {
                            justify-content: end;
                            padding-right: 20px;
                        }
                    }
                }
            }

            .optional-radios {
                padding: 32px 0;
                display: flex;

