                    margin-bottom: 0px;
                    margin-top: 2px;
                }

                label {
                    font-size: 16px;
                    line-height: 24px;
                }

                .mandatory-radios,
                .optional-radios {
                    flex-direction: column;
                    padding: 0;
                }

                .input-group {
                    padding: 24px 0 0 0;

                    div {
                        min-width: 165px;
                        text-align: left;
                    }
                }
            }
        }

        p {
            font-style: normal;
            font-weight: 400;
            font-size: 18px;
            line-height: 32px;
            text-align: center;
            color: #111111;

            a {
                color: #005AA3;
                font-weight: 700;
                text-decoration: underline;
            }

            @include respond-to("sm-down") {
                font-size: 16px;
                line-height: 24px;
                padding-top: 40px;
            }
        }

        div.button {
            .btn {
                padding: 13px 30px;
                margin: 32px;
                font-size: 16px;
                line-height: 24px;

                @include respond-to("sm-down") {
                    margin: 24px;
                }
            }

            .cancel {
                color: #005AA3;
                font-weight: 700;
                font-size: 16px;
                line-height: 24px;
                text-decoration-line: underline;
            }
        }
    }
}

