.panel-group.accordion{
    .panel{
        background: none;
        border:none;
        border-radius: 0;
        box-shadow: none;
        &+.panel {
            margin-top: 0;    
        }
        &:first-child(){
            .panel-heading{
                border-top: 1px solid lighten($color-gray-plain, 10%);
            }  
        }
        &:last-child(){
            .panel-heading{
                border-top: none;
            }  
        }
        &-default{
            > .panel-heading{
                background: none;
                border-radius: 0;
                border-bottom: 1px solid lighten($color-gray-plain, 10%);
                padding:20px 0;
                .panel-title{
                    position: relative;
                    span{
                        color: #000000;
                        cursor:pointer;
                        font-size: em(16);
                        line-height: 1.5;
                        text-align: left;
                        width: 92%;
                        display: block;
                        @extend %font-normal;
                       
                        &:after{
                            content:"\f068";
                            color: $color-blue;
                            float: right;
                            font-family: fontAwesome;
                            font-size:em(10);
                            @extend %font-normal;
                            position: absolute;
                            right: 0;
                            top: 30%;
                        }
                        &.collapsed{
                            &:after{
                                content:"\f067";
                            }   
                        }
                        @include respond-to('md'){
                            font-size: em(21);
                        }
                    }//span
                }//panel-title
            }//panel-heading
            .panel-body{
                border:none;
                padding: 30px 0;
                //border-bottom: 1px solid lighten($color-gray-plain, 10%);
                color:$color-black-alpha;
            }
        }//deafult
    }//panel
}//accordion

