section.new-design {
	@extend %transition;
	position: relative;

	&:after {
		@extend .clearfix;
	}

	ul.navbar-nav {
		padding: 0;

		li {
			margin: 0;
		}

		>li.dropdown.open .dropdown-menu {
			@include respond-to("md") {
				border-bottom-left-radius: 10px;
				border-bottom-right-radius: 10px;
				right: 0;
				margin: auto;
				width: 90%;
				max-width: 1280px;
				font-size: 16px;
			}
		}
	}

	.navbar.navbar-default {
		background: $color-blue;
		border: none;
		z-index: 4;
		margin-bottom: 0;
		border-radius: 0;
		padding-bottom: 0;
		box-shadow: none;
		width: 100%;

		a.navbar-brand {
			//delete old design
			width: unset;
			height: auto;
			padding: 0;

			img {
				width: 100px;

				@include respond-to("lg") {
					width: 173px;
					margin-right: 20px;
				}
			}
		}

		.container-fluid {
			max-width: 1280px;
			display: flex;
			align-items: center;
			width: 95%;
			height: 56px;

			@include respond-to("lg") {
				height: 84px;
			}
		}

		.navbar-collapse {
			border: none;

			@include respond-to('lg-down') {
				border-top: 1px solid transparent;
				box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1);

				&.collapse {
					display: none !important;
				}

				&.collapse.in {
					display: block !important;
				}
			}
		}

		.navbar-nav {
			>li {
				>a {
					color: $color-white;
					font-weight: 700;
					text-transform: capitalize;
					line-height: 55px;
					font-size: 16px;
					height: 69px;
					border-bottom: 4px solid transparent;
					padding: 0;
					margin: 15px 20px;
					margin-bottom: 0;
					@extend %transition;

					&:hover,
					&:focus {
						border-bottom: 4px solid $color-cyan;
						outline: none;
					}
				}

				&.open {
					>a {
						background: none;
						@extend %transition;
						border-bottom: 4px solid $color-cyan;

						&:hover,
						&:focus {
							color: $color-white;
						}
					}
				}
			}
		}

		.navbar-toggle {
			border: none;
			float: left;
			margin: 0;
			margin-left: 20px;
			padding-right: 0;
			display: none;

			&:hover,
			&:focus {
				background: none;
			}

			&:focus {
				outline: -webkit-focus-ring-color auto 1px;
			}

			&.collapsed {
				&:after {
					content: none;
				}
			}

			~.icon-close {
				width: 40px;
				height: 40px;
				background: url('../images/new-icons/close-menu-mobile.svg') no-repeat;
				margin-top: 3px;
				margin-left: 20px;
				margin-right: -10px;
			}

			.icon-bar {
				width: 20px;
				background-color: $color-white;
			}

			&.collapsed {
				display: block;

				~.icon-close {
					display: none;
				}
			}
		}

		.nav-center {
			@extend .flex-center;
			padding-left: 0;
		}

		.nav-right {
			@extend .flex-center;
			justify-content: flex-end;
			flex: 1;

			.avatar-container {
				position: relative;
				display: inline-block;

				img {
					margin-left: 5px;
				}

				.avatar-hot {
					height: 28px;
					width: 28px;
				}

				.avatar-cold {
					height: 20px;
					width: 19px;
					vertical-align: text-top;
				}

				.fa.fa-circle {
					position: absolute;
					top: 28px;
					left: 25px;
					font-size: em(12);
				}
			}

			.nav-right-desktop {
				position: relative;
				@extend .flex-center;
				margin-right: -10px;

				>ul,
				>li {
					padding: 0;
					margin: 0;
					list-style: none;
				}

				>.dropdown {
					>a {
						display: block;
						height: 84px;
						line-height: 84px;
						border-bottom: 4px solid transparent;
						@extend %transition;

						&:focus,
						&:hover {
							outline: none;
							border-bottom: 4px solid $color-cyan;
						}
					}

					.open {
						border-bottom: 4px solid $color-cyan;
					}
				}

				.navbar-text {
					font-weight: 600;
					color: $color-white;
					margin: 0;
					display: flex;
					align-items: center;

					.name {
						min-width: 58px;
						@extend %ellipsis;
					}
				}

				&.warm-hot {
					.dropdown {
						.up-down-arrow {
							width: 10px;
							height: 8px;
							@extend %arrow-down-custom;
						}

						&.open {
							.up-down-arrow {
								@extend %arrow-up-custom;
							}
						}
					}
				}

				.divider {
					width: 1px;
					height: 43px;
					background: $color-white;
					opacity: 0.3;
					margin: 0 20px;
				}

				.scWebEditInput {
					width: unset;
				}

				.login-curtain {
					display: none;
					position: absolute;
					width: 235px;
					padding: 25px 20px;
					background-color: white;
					border-bottom-left-radius: 10px;
					border-bottom-right-radius: 10px;
					left: 50%;
					-webkit-transform: translateX(-50%);
					transform: translateX(-50%);
					box-shadow: 0px 3px 5px 2px rgba(0, 0, 0, 0.2);

					a.btn {
						display: block;
						width: 100%;
						padding: 8px;
						font-size: 14px;
						font-weight: 600;
						margin-bottom: 10px;
					}

					p {
						font-size: 11px;
						font-weight: 600;
						color: $color-blue;
						margin-bottom: 0;
						padding-bottom: 0;
						line-height: 1;
						text-align: center;
					}
				}
			}

			.nav-right-mobile {
				@extend .flex-center;

				.close-icon {
					vertical-align: middle;
					width: 10px;
					color: transparent;
					font-size: 20px;
					margin-left: 0;
					line-height: 1;
				}

				.fa.fa-circle {
					top: 0 !important;
				}

				&.warm-hot {
					.up-down-arrow {
						width: 10px;
						height: 6px;
						@extend %arrow-down-custom;
					}

					.close-icon {
						display: none;
					}

					.collapsed {
						.up-down-arrow {
							display: none;
						}

						.close-icon {
							color: $color-primary-beige;
							display: inline-block;
						}
					}
				}

				&.cold {
					.avatar-cold {
						margin-right: 4px;
					}

					.avatar-cold-circle {
						display: none;
					}

					.collapsed {
						.avatar-cold {
							display: none;
						}

						.avatar-cold-circle {
							display: inline-block;
						}

						.close-icon {
							margin-top: 0px;
							margin-left: 1px;
							color: $color-primary-beige;
							display: inline-block;
						}
					}
				}
			}
		}

		.mega-dropdown-menu {
			padding-top: 0;
			padding-bottom: 40px;
			border: none;

			.container {
				@include respond-to("md") {
					width: inherit;
				}

				padding-left: 193px;
				margin-left: 15px;

				li,
				ul {
					list-style: none;
					padding: 0;
					margin: 0;
				}

				>li {
					margin-right: 40px;
					margin-top: 30px;

					li.dropdown-header {
						color: $color-alto-dark;
						font-size: 12px;
						line-height: 16.5px;
						padding-bottom: 10px;
						display: flex;
						align-items: center;
						font-weight: 700;

						&:not(:first-child) {
							padding-top: 30px;
						}

						&:after {
							flex-shrink: 1;
							flex-grow: 1;
							content: "";
							display: block;
							background-color: $color-primary-beige-medium;
							height: 1px;
							margin-left: 0.6em;
							margin-top: 2px;
						}
					}
				}

				a {
					line-height: 30px;
					display: block;
					color: $color-blue;
					font-weight: 400;
					padding-bottom: 10px;
					padding-top: 5px;
					font-size: 14px;
				}

				&.four-columns {
					margin-left: auto;
					padding-left: 20px;

					>li {
						margin-right: 0;
						padding-right: 30px;
					}

					li {
						a {
							line-height: 24px;
						}
					}
				}
				&.five-columns {
					display: grid;
					grid-template-columns: repeat(5, 1fr);
					grid-template-rows: 1fr;
					grid-column-gap: 0px;
					grid-row-gap: 0px;
					margin-right: 0px;
					padding-left: 20px;
					width: 100%;

					.col1 { grid-area: 1 / 1 / 2 / 2; }
					.col2 { grid-area: 1 / 2 / 2 / 3; }
					.col3 { grid-area: 1 / 3 / 2 / 4; }
					.col4 { grid-area: 1 / 4 / 2 / 5; }
					.col5 { grid-area: 1 / 5 / 2 / 6; }
					
				}
			}
		}

		.mega-dropdown {
			position: static !important;
			width: 100%;
			padding: 0;

			@include respond-to("lg") {
				width: auto;
			}

			>a.dropdown-toggle {
				&:after {
					@extend %arrow-down-custom;
					margin-left: 6px;
					width: 10px;
					height: 8px;
				}
			}

			&.open {
				>a.dropdown-toggle {
					&:after {
						@extend %arrow-up-custom;
						margin-left: 5px;
						width: 11px;
						height: 9px;
					}
				}
			}

			&.no-icon {
				>a.dropdown-toggle {
					&:after {
						content: " ";
					}
				}
			}
		}
	}

	.card-container {
		@extend .flex-center;

		.header-card {
			max-width: 121px;
			height: auto;

			@include respond-to("lg") {
				max-width: 70px;
			}
		}

		div.card-text {
			display: flex;
			flex-direction: column;
			font-weight: 600;
			margin-left: 20px;

			@include respond-to("lg") {
				width: 140px;
				margin-left: 7px;
				margin-right: -10px;
			}

			.product-name {
				color: $color-alto-light;
				font-size: 12px;
				line-height: 1.4;
				padding-bottom: 2px;
				width: 70%;

				@include respond-to("lg") {
					font-size: 10px;
					width: 100%;
				}
			}

			.product-action {
				text-transform: uppercase;
				font-style: italic;
				font-size: 16px;
				letter-spacing: 0.0556em;
				color: $color-blue;

				@include respond-to("lg") {
					color: $color-white;
					font-size: 10px;
					line-height: 14px;
				}
			}
		}

		i {
			&:after {
				content: " ";
				vertical-align: middle;
				display: inline-block;
				background: url(../images/new-icons/chevron-right-link-blue.svg) no-repeat;
				width: 26px;
				height: 26px;

				@include respond-to("lg") {
					background: url(../images/new-icons/chevron-right-link-white.svg) no-repeat;
				}
			}
		}
	}

	.alert-text {
		font-weight: 700;
		font-style: italic;
		color: $color-blue;
	}

	.profile-menu {
		min-width: 264px;
		box-shadow: 0px 12px 20px rgba(0, 45, 82, 0.5);
		top: 82px;
		right: -60px;
		left: auto;
		border-radius: 8px;
		border-top-left-radius: 0;
		border-top-right-radius: 0;
		border: none;
		padding: 40px 25px;

		a {
			font-size: 14px;
			font-weight: 400;
			width: 100%;
			color: $color-blue;
			line-height: 23px;

			&:empty {
				display: none;
			}
		}

	}

	.profile-links {
		@extend %profile-links;

		>a {
			&:last-child {
				border-bottom: 1px solid $color-primary-beige-medium;
				padding-bottom: 20px;
			}
		}

		&:after {
			@extend .clearfix;
		}
	}

	.profile-actions {
		a {
			color: $color-alto-dark;

			&.blue {
				color: $color-blue  !important;
				text-decoration: underline;
			}
		}

		.pipeline {
			padding-right: 10px;
			padding-left: 10px;

			&::after {
				content: "\007C";
			}
		}
	}

	.profile-menu-accordion {
		&.active {
			&:after {
				@extend %arrow-up-fa;
			}
		}

		&:after {
			@extend %arrow-down-fa;
		}

		&:hover,
		&:focus {
			outline: none;
			text-decoration: none;
		}
	}

	.profile-menu-actions {
		background-color: $color-primary-beige;
		margin-left: -25px;
		margin-right: -25px;
		display: none;
		overflow: hidden;
		padding-bottom: 0;

		li {
			margin: 0;
			position: relative;

			&:last-of-type {
				a {
					border-bottom: 0;
				}
			}
		}

		a {
			display: block;
			clear: both;
			padding: 10px 0px;
			border-bottom: 1px solid #e7e4df;
			width: 80% !important;
			margin: 0 auto;
		}

		.fa.fa-circle {
			right: 25px;
			@extend %fa-circle;
		}
	}

	.state-img {
		width: 58px;
		margin-right: 10px;
		border-radius: 4px;
	}

	//Mobile Layers
	.overlay-mobile {
		height: 0%;
		width: 100%;
		position: fixed;
		z-index: 50;
		left: 0;
		background-color: $color-white;
		overflow-y: auto;
		transition: .5s ease;
		opacity: 0;

		.overlay-content {
			position: relative;
			width: 100%;
			height: 100%;

			>ul {
				list-style: none;
				margin: 0;

				// first level menu links
				>li {
					margin: 0;

					&:nth-last-child(2) {
						>a:last-child {
							border-bottom: none;
						}
					}

					>a {

						&:focus,
						&:hover {
							outline: none;
						}
					}
				}

				// first level menu
				a {
					margin: 0px 24px;
					padding: 24px 0;
					text-decoration: none;
					font-size: 18px;
					color: $color-blue;
					display: block;
					transition: 0.3s;
					line-height: 25px;
					font-weight: 700;
					border-bottom: 1px solid #e7e4df;

					&.n-b-b {
						border-bottom: 0;
					}

					&.dropdown-toggle {
						&:after {
							@extend %arrow-down-blue;
							width: 15px;
							height: 8px;
							position: relative;
							float: right;
							top: 8px;
						}
					}
				}

				.open {
					>a.dropdown-toggle {
						border-bottom: 1px solid transparent;

						&:after {
							@extend %arrow-up-blue;
						}
					}
				}
			}

			// second level menu - position
			.open ul.dropdown-menu {
				position: static;
				float: none;
				width: auto;
				border: none;
				padding: 0;
				margin: 0;
				box-shadow: none;
			}

			// second level menu
			ul.dropdown-menu {
				background-color: $color-primary-beige;

				>li {
					margin: 0 24px;

					@include respond-to("md") {
						display: list-item;
						height: auto;
					}

					>a {
						color: $color-blue;
						font-size: 16px;
						line-height: 52px;
						padding: 0;
						margin: 0;
						font-weight: 400;

						&:focus {
							outline: none;
						}
					}
				}
			}

			// third level menu
			.dropdown-submenu {
				position: relative;

				>a {
					text-transform: capitalize;
					display: block;

					&:after {
						@extend %arrow-down-fa;
						margin-right: 12px;
					}

					&.open {
						&:after {
							@extend %arrow-up-fa;
						}

						border-bottom: 1px solid transparent;
					}
				}

				.dropdown-menu {
					a {
						font-size: 14px;
						padding: 0 16px;
						border-bottom: 1px solid #f4f3ef;

						&:hover,
						&:focus {
							background-color: inherit;
						}
					}
				}

				.open~ul.dropdown-menu {
					background-color: $color-white;
					margin-right: -24px;
					margin-left: -24px;
					border-radius: 0px;
				}
			}
		}

		#profile-menu {
			background-color: $color-primary-beige;

			.profile-header {
				height: 72px;
				display: flex;
				align-items: center;
				margin: 0 24px;

				.profile-name {
					img {
						@extend .state-img;
					}

					span.name {
						font-weight: 600;
						font-size: 16px;
						color: $color-alto-dark;
					}
				}

				.profile-actions {
					justify-content: flex-end;
					display: flex;
					flex: 1;

					a {
						font-size: 12px;
						color: $color-alto-dark;
						font-weight: 400;
						padding-right: 6px;
					}

					.pipeline {
						font-size: 12px;
						padding-right: 12px;
					}
				}
			}

			.profile-links {
				background-color: $color-white;
				padding: 24px;

				>a {
					font-size: 14px;
					border-bottom: 1px solid #f4f3ef;
					line-height: 50px;
					padding-top: 0;
					padding-bottom: 0;

					&:last-child {
						border-bottom: none;
					}
				}
			}

			.profile-menu-actions {
				a {
					padding: 10px 25px;
					font-size: 14px;
					width: 100% !important;
					position: relative;
				}

				.fa.fa-circle {
					right: 25px;
					@extend %fa-circle;
				}
			}
		}

		.get-started {
			background-color: $color-white;
			padding: 40px 24px;

			.actions {
				.btn {
					background-color: $color-blue;
					padding: 10px 30px;
					font-size: 16px;
					color: $color-white;
					font-weight: 600;

					&:hover,
					&:focus {
						background-color: $color-cta-primary-roll;
					}
				}

				.link {
					display: block;
					padding-top: 20px;
					margin-bottom: 20px;
				}
			}
		}

		.card-container {
			margin: 24px;
			margin-bottom: 5px;
		}

		.dropdown-backdrop {
			display: none;
		}
	}
}

html.disableScrollSafari,
body.disableScrollSafari {
	overflow-y: hidden !important;
	position: fixed !important;
	height: 100% !important;
}

.subnav-headline {
	.slick-disabled {
		display: none !important;
	}

	display: grid;
	grid-template-columns: 8% 84% 8%;
	background-color: $color-gray;
	height: 56px;
	@include respond-to("sm") {
		grid-template-columns: 10% 80% 10%;
	}
	@include respond-to("md") {
		display: flex;
		height: 84px;
	}
	
	.subnav-links-container {
		@include respond-to("md") {
			display: flex;
			align-items: center;
			max-width: 1200px;
			width: 95%;
    		margin: 0 auto;
		}

		@include respond-to("lg") {
			max-width: 1280px;
		}
		.menu-slider {
			@include respond-to("md") {
				display: flex;
			}

			.slick-slide {
				align-items: center;
    			justify-content: center;
			}
			
			a {
				display: flex !important;
				height: 56px;
				align-items: center;
				justify-content: center;
				font-size: 14.5px;

				&:hover,
				&:focus {
					color: $color-blue;
				}

				@include respond-to("md") {
				 	height: unset;
					padding-left: 15px;
					padding-right: 15px;

					&:first-of-type {
						padding-left: 0;
					}
				} 	
				
				@include respond-to("lg") {
					font-size: 16px;
					padding-left: 15px;
					padding-right: 15px;
				}
		
				&.active {
					font-weight: bold;
				}

				&.right-cta {	
					height: 40px;
					@include respond-to("md") {
						display: none !important;
				   }
				}
			}
		}
		a.right-cta {
			background-color: white;
			font-weight: bold;
			margin-left: auto;
			padding: 10px 25px;
			border-radius: 4px;
			
			@include respond-to("md-down") {
				display: none;
		   	}
		}
	}

	.subnav-slider-navigation {	
		.back-arrow,
		.next-arrow {
			cursor: pointer;
			font-size: 34px;
			width: 100%;
			display: inline-block;
    		text-align: center;
			padding: 0;
			height: 80%;
			color: #6e6e6e;

			span {
				height: 100%;
				display: flex;
				align-items: center;
				justify-content: center;
			}
		}

		.back-arrow {
			border-right: 1px solid $color-text-gray;
		}

		.next-arrow {
			border-left: 1px solid $color-text-gray;
		}
	}
}